import React from 'react'
import { useState } from 'react'
import swal from 'sweetalert';
import axios from 'axios';
import { Link } from 'react-router-dom';
const QRCode = require('qrcode');
function Input({ addData }) {
    const [input, setInput] = useState({ serialNumber: '', type: '', brand: '', attribute: '', location: '' });
    const [file, setFile] = useState({ fileSelect: null, fileSelectName: '' })
    const handleSubmit = async (e) => {
        e.preventDefault();
        console.log(JSON.stringify(input))
        swal({
            title: "กรุณาตรวจสอบข้อมูลอีกครั้ง ?",
            text: "โปรดตรวจสอบข้อมูล ก่อนยืนยันอีกครั้ง",
            icon: "warning",
            buttons: true,
            dangerMode: true,
        })
            .then((check) => {
                if (check) {
                    swal("ระบบได้ทำการส่งข้อมูลเรียบร้อยแล้ว", {
                        icon: "success",
                    }).then(addData(input))
                        .then(axios.post('https://o5fee59np0.execute-api.ap-southeast-1.amazonaws.com/development/cloud', JSON.stringify(input), {
                            headers: {
                                "Content-Type": 'application/json'
                            }
                        }
                        )).then(Qrcode)
                        .then(<Link to='/borrow'></Link>)
                        .catch((error) => {
                            console.log(error)
                            swal("มีบางอย่างผิดพลาด", {
                                icon: "warning",
                            })
                        })
                } else {
                    swal("ระบบไม่ได้ทำการส่งข้อมูลดังกล่าว!");
                }
            });
    }
    const Qrcode = (e) => {
        QRCode.toDataURL(`https://main.d3gbohtlq9xzn0.amplifyapp.com/#/borrow/${input.serialNumber}`, function (err, base64) {
            var win = window.open();
            win.document.write('<iframe src="' + base64 + '" frameborder="0" style="border:0; top:0px; left:0px; bottom:0px; right:0px; width:100%; height:100%;" allowfullscreen></iframe>');
        });
    }



    const onChange = (e) => {
        e.preventDefault();
        const { id, value } = e.target;
        setInput({ ...input, [id]: value });

    }

    const onFileChange = (e) => {
        e.preventDefault();
        setFile({ fileSelect: e.target.files[0], fileSelectName: e.target.files[0].name })
    }

    const onFileUpload = (e) => {
        e.preventDefault();
        const formData = new FormData();
        formData.append(
            "myFile",
            file.fileSelect,
            file.name
        );

        const reader = new FileReader()
        reader.onload = async (f) => {
            const text = (f.target.result)
            console.log(text)
            const array = text.split(/\r?\n/)
            //maybe bug
        };
        reader.readAsText(file.fileSelect)
    }
    return (
        <div>
            <div className="card">
                <div className="card-header h5">Register Form</div>
                <div className="card-body my-3">
                    <form onSubmit=
                        {handleSubmit} >
                        <div className="form-row">
                            <div className="form-group col-md-4">
                                <label htmlFor="serialNumber">Serial Number</label>
                                <input type="text" className="form-control thaifont" id="serialNumber" placeholder="PnXL7VB-gBN2K4HTwkqXnJDDwBgC" value={input.serialNumber} onChange={onChange} required />
                            </div>
                            <div className="form-group col-md-2">
                                <label htmlFor="type">Type</label>
                                <input type="text" className="form-control thaifont" id="type" placeholder="Inactive monitor" value={input.type} onChange={onChange} required />
                            </div>
                            <div className="form-group col-md-3">
                                <label htmlFor="brand">Brand</label>
                                <input type="text" className="form-control thaifont" id="brand" placeholder="Dell , Asus , Etc." value={input.brand} onChange={onChange} required />
                            </div>
                            <div className="form-group col-md-3">
                                <label htmlFor="location">Location</label>
                                <input type="text" className="form-control thaifont" id="location" placeholder="LC2-111" value={input.location} onChange={onChange} required />
                            </div>
                        </div>

                        <div className="form-row">
                            <div className="form-group col-md-12">
                                <label htmlFor="arttibute">Attribute</label>
                                <input type="text" className="form-control thaifont" id="attribute" placeholder="จอสำหรับใช้เป็นสื่อการเรียนการสอนในห้อง 111 ตึกบรรยายเรียนรวม 2  มีขนาดความกว้าง 34 นิ้ว เหมาะสำหรับการเรียนการสอน 30-50 คน" value={input.attribute} onChange={onChange} required />
                            </div>
                        </div>
                        <input type="submit" disabled={false}
                            className="btn btn-primary thaifont w-100" style={{ "fontSize": "20px" }} value="บันทึก" />
                    </form>
                </div>
            </div>
            <div className="card mt-5">
                <div className="card-header h5">Register From Batch</div>
                <div className="card-body my-3">
                    <form>
                        <div className="form-row">
                            <div className="form-group col-md-8">
                                <label htmlFor="serialNumber">Select file from computer</label>
                                <div className="input-group mb-3">
                                    <div className="input-group-prepend">
                                        <span className="input-group-text" id="fileSelect">Upload</span>
                                    </div>
                                    <div className="custom-file">
                                        <input type="file" className="custom-file-input" id="fileSelect" aria-describedby="fileSelect" onChange={onFileChange} required />
                                        <label className="custom-file-label" accept=".txt, .xlsx, .csv " htmlFor="fileSelect">{file.fileSelectName ? file.fileSelectName : 'Choose File'}  </label>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <input type="submit" disabled={false}
                            className="btn btn-primary thaifont w-100" style={{ "fontSize": "20px" }} value="บันทึก" onClick={onFileUpload} />
                    </form>
                </div>
            </div>
        </div>
    )

}
export default Input;
