import './App.css';
import Navbar from './Components/Navbar';
import Input from './Components/Input'
import { useState } from 'react'
import { Route, Switch } from 'react-router-dom'
import Borrow from './Components/Borrow';

function App() {
  const [info, setInfo] = useState([{}]);
  const [binfo, bsetInfo] = useState([{}]);

  const baddData = (e) => {
    bsetInfo([...binfo, { personalId: e.personalId, role: e.role, duration: e.duration, detail: e.detail }])
  }

  const addData = (e) => {
    setInfo([...info,
    {
      serial: e.serialNumber,
      type: e.type,
      brand: e.brand,
      attribute: e.attribute,
      location: e.location
    }

    ])
  }


  const input = () => (
    <Input
      addData={addData} />
  );

  const borrow = () => (
    <Borrow
      addData={baddData}
    />
  );

  return (
    <div className="App">
      <Navbar />
      <div className="App container">
        <Switch>
          <Route exact path="/" component={input} />
          <Route path="/borrow/:id" component={borrow}/>
        </Switch>
      </div>
    </div>
  );
}

export default App;
